.button--primary {
  background-color: #07977D;
  color: white;
  text-transform: uppercase;
}
.button--primary:hover {
  color: #07977D;
  background-color: white;
}

.button--arrow i {
  color: #07977D;
}

.paragraph--type--banner {
  display: flex;
  height: 600px;
  align-items: center;
}
.paragraph--type--banner .field--name-field-text {
  color: white;
  font-size: 60px;
  padding: 3rem;
  max-width: 60%;
  padding-bottom: 0;
  padding-top: 0;
}
.paragraph--type--banner .paragraph--type--cta {
  padding: 3rem;
}

html {
  font-size: 16px;
  line-height: 26px;
  color: #343338;
  font-family: muli, sans-serif;
}

h1 {
  font-size: 60px;
  line-height: 75px;
}

.node--type-article .profile img {
  border-radius: 50%;
  width: 100px;
  height: 100px;
}

a {
  color: #07977D;
}

.site-logo img {
  height: 80px;
}

.menu--account li.nav-item:last-child {
  background: #07977D;
}

.menu--account li.nav-item:last-child a {
  color: white;
}

#navbarNav {
  height: 80px;
}

@media screen and (min-width: 678px) {
  .region-primary-menu {
    display: flex;
  }

  .region-secondary-menu, .region-primary-menu, .navbar-nav,
.region-primary-menu nav, .region-secondary-menu nav {
    height: 100%;
  }
}
.region-primary-menu .navbar-nav li a,
.region-secondary-menu .navbar-nav li a {
  height: 100%;
  display: flex;
  align-items: center;
}

.menu a {
  position: relative;
  text-decoration: none;
}

.menu a:hover::after {
  content: "";
  position: absolute;
  left: 7%;
  right: 7%;
  top: 70%;
  border: 1px solid #07977D;
}

.block-page-title-block {
  background: #07977D;
  padding: 50px;
  margin-bottom: 30px;
  border-bottom-right-radius: 100px;
  color: white;
}